import React, { useEffect, useMemo, useRef, useState } from "react";
import { Card, Form, Modal, Col, Row, Button } from "react-bootstrap";
import { AiFillEye } from "react-icons/ai";
import { BiSearch } from "react-icons/bi";
import { BsDownload } from "react-icons/bs";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import PrintComponents from "react-print-components";

import { getPaymentDetails } from "../../../app/features/paymentDetails/paymentDetails.slice";
import InvoicePDF from "../../../components/Theme/InvoicePDF";
import OnlineFormInvoices from "./OnlineFormInvoices";
import DataTable from "../../../components/Table/DataTable";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import GenericDatePicker from "../../../components/GenericDatePicker";
import SearchInput from "../../../components/SearchInput";

export default function AdminPaymentDetails() {
  const [selectedDate, setSelectedDate] = useState();
  const [show, setShow] = useState(false);
  const [key, setKey] = useState(false);
  const [viewInvoice, setViewInvoice] = useState();
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 10, []);
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const dispatch = useDispatch();
  const printButtonRef = useRef(null);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const { allPaymentDetails, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.PaymentDetails
  );

  const handlePrintClick = () => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
  };

  const viewInvoiceData = (data) => {
    handleShow();
    setViewInvoice(data);
  };

  const columns = [
    {
      dataField: "invoiceNumber",
      text: "Invoice Number",
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "patientName",
      text: "Patient Name",
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "amount",
      text: "Amount",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return cell ? moment(cell).format("DD/MM/YYYY") : "N/A";
      }, 
    },
    {
      dataField: "status",
      text: "Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 custom-width-class ${
            cell?.status === "Paid" && "active-status"
          }`}
        >
          {cell?.status === "Paid" && "Paid"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, data) => (
        <div
          className="w-100 text-center"
          style={{ color: "#3F8BFC", cursor: "pointer" }}
          onClick={() => viewInvoiceData(data)}
        >
          <AiFillEye className="me-2" />
          <span>View</span>
        </div>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
    },
  ];

  useEffect(() => {
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
      patientId: null,
      search: searchValue ? searchValue : null,
      createdDate: selectedDate
        ? moment(selectedDate).format("YYYY-MM-DD")
        : null,
    };
    dispatch(getPaymentDetails(finalData));
  }, [dispatch, searchValue, selectedDate, pageNumber]);

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  return (
    <div className="doctor-patient-paymentdetails">
      <div
        className="usermanagement-mainclass"
        style={{ marginBottom: "1.6rem" }}
      >
        <Button
          variant="primary"
          onClick={() => setKey(false)}
          className={`Admin-Tabs-All me-2 mb-2 ${
            key === false ? "nav-btn-active" : "nav-btn"
          }`}
        >
          Appointment Receipt
        </Button>
        <Button
          variant="primary"
          onClick={() => setKey(true)}
          className={`Admin-Tabs-All me-2 mb-2 ${
            key === true ? "nav-btn-active" : "nav-btn"
          }`}
        >
          Online Prescription Receipt
        </Button>
      </div>
      {key === false ? (
        <Card className="patient-details-card superadmin-date-wrapper">
          <Row className="mt-3 mx-2">
            <Col md={3}>
                <SearchInput onChange={handleSearch} />
            </Col>

            <Col
              md={9}
              className="d-flex flex-wrap align-self-center justify-content-end"
            >
              <GenericDatePicker
                selectedDateState={selectedDate}
                onChange={(date) => setSelectedDate(date)}
              />
            </Col>
          </Row>

          {isLoading ? (
            <FullScreenLoader fullHeight={true} />
          ) : isSuccess ? (
            <span className="doctor-datatable">
              <DataTable
                tableColumns={columns}
                tableData={
                  allPaymentDetails?.data ? allPaymentDetails?.data : []
                }
                keyField="invoiceNumber"
                pageNumber={pageNumber}
                totalRecords={allPaymentDetails?.totalCounts}
                dataPerPage={pageSize}
                handlePageChange={handlePageChange}
              />
            </span>
          ) : isError ? (
            <p className="text-center text-danger fst-italic my-5">
              Network Error
            </p>
          ) : null}
        </Card>
      ) : (
        <OnlineFormInvoices key={key} />
      )}
      <Modal
        show={show}
        onHide={handleClose}
        size="xl"
        centered
        className="modal-main"
        backdrop="static"
      >
        <Modal.Header
          className="border border-0 Payment-Details-MainClass"
          closeButton
        />
        <Modal.Body className="pt-0">
          <div className="w-100 d-flex justify-content-between align-items-center">
            <p
              className="mb-0  Invoice-Number fw-bold"
              style={{ color: "#999999" }}
            >
              Invoice {viewInvoice?.invoiceNumber}
            </p>
            <button
              className="download-button-class ps-4 pe-4 rounded"
              onClick={handlePrintClick}
            >
              <BsDownload className="me-2" />
              Download
            </button>
          </div>
          <div className="d-flex justify-content-between align-items-center">
            <div className="d-flex align-items-center">
              <div>
                <img
                  src={
                    viewInvoice?.doctorProfileImage
                      ? viewInvoice?.doctorProfileImage
                      : `https://ui-avatars.com/api/?name=${`doctorName`}&background=000071&color=fff`
                  }
                  alt="patient"
                  className="rounded-5 object-fit-cover"
                  style={{ width: "4rem", height: "4rem" }}
                />
              </div>
              <div className="ms-3">
                <p className="fw-bold m-0">Dr. {viewInvoice?.doctorName}</p>
                <p
                  className="m-0"
                  style={{ color: "#999999", fontSize: "14px" }}
                >
                  {moment(viewInvoice?.createdDate).format("DD MMM YYYY")}
                </p>
              </div>
            </div>
            <div>
              <p
                className="px-4 rounded m-0"
                style={{
                  color: "#44BC19",
                  backgroundColor: "#ECFFCC",
                  border: "none",
                }}
              >
                {viewInvoice?.status}
              </p>
            </div>
          </div>
          <hr style={{ color: "#c8cfd5" }} />
          <Row>
            <Col lg={6}>
              <div className="mt-3">
                <label
                  htmlFor=""
                  className="labeling-Fontsize"
                  style={{ color: "#B3B3B3" }}
                >
                  From:
                </label>
                <h5 className="fs-6 fw-bold">{viewInvoice?.patientName}</h5>
                <p className="Address-FontSize fw-bold mb-2">
                  {viewInvoice?.location}
                </p>
                <small>{viewInvoice?.email}</small>
                <p className="Address-FontSize mt-2">
                  {viewInvoice?.phoneNumber}
                </p>
              </div>
            </Col>
            <Col lg={6} className="Bill-To-Class">
              <div className="mt-3">
                <label
                  htmlFor=""
                  className="labeling-Fontsize"
                  style={{ color: "#B3B3B3" }}
                >
                  Bill to:
                </label>
                <h5 className="fs-6 fw-bold">
                  FamilyDoc Medical Services Limited
                </h5>
                <p className="Address-FontSize fw-bold mb-2">
                  Address: 77 Camden street Lower, Dublin 2
                </p>
                <small>contact@familydoc247.ie</small>
                <p className="Address-FontSize mt-2">+353 19069327</p>
              </div>
            </Col>
          </Row>
          <hr style={{ color: "#c8cfd5" }} />
          <div>
            <h5 className="fs-6 fw-bold mt-4">Description</h5>
            <div
              className="d-flex justify-content-between mt-4 Description-Bar"
              style={{ backgroundColor: "#F1F1F1", padding: "5px" }}
            >
              <h6 className="ps-3 m-0" style={{ color: "#999999" }}>
                Reason
              </h6>
              <h6 className="pe-5 m-0" style={{ color: "#999999" }}>
                Fee
              </h6>
            </div>
          </div>
          <div className="fs-6 fw-bold d-flex justify-content-between mt-4">
            <h6
              style={{ color: "#1A1A1A" }}
              className="fw-bold Description-Bar px-2"
            >
              {/* {viewInvoice?.appointmentReason
                ? viewInvoice?.appointmentReason
                : "N/A"} */}
              Medical Services
            </h6>
            <h6 className="fw-bold me-5">
              {viewInvoice?.amount ? `€${viewInvoice?.amount}` : "N/A"}
            </h6>
          </div>
        </Modal.Body>
      </Modal>
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="border-0 px-3 py-2 rounded-2 d-none"
          >
            <BsDownload size={20} /> Download PDF
          </button>
        }
      >
        <InvoicePDF invoiceData={viewInvoice} />
      </PrintComponents>
    </div>
  );
}
